@import url('https://fonts.googleapis.com/css?family=Libre+Baskerville');

*{
    margin :0;
    padding:0;
    font-family: Libre Baskerville;
}


.wrapper{
    width:100%;
}

.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 0.3cm;
    padding-top: 0.8cm;
    overflow: hidden;
    
}
.col1{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
 .row2, .row3, .row4, .row5, .row6{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 0.5cm;
}

.row1coltype{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#result{
    border:thin solid grey;
    padding: 0.2cm;
    width: 7.5cm;
    height:1cm;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

}
.b1{
    padding: 0.5cm;
    border:thin solid grey;
    background-color: rgb(183, 135, 242);
    border-radius: 50%;
    width:1.6cm;
    align-content: center;
    color: #ffffff;
    font-size: 18px;
}
.bimg{
    height: 0.8cm;
    width:0.8cm;
}
.b2{
    padding: 0.5cm;
    border:thin solid grey;
    background-color: rgb(111, 0, 246);
    border-radius: 50%;
    width:1.6cm;
    align-content: center;
    color: #ffffff;
    font-size: 18px;
}

.b3{
    padding: 0.5cm;
    border:thin solid grey;
    background-color: rgb(0, 0, 0);
    border-radius: 50%;
    width:1.6cm;
    align-content: center;
    color: #ffffff;
    font-size: 18px;
}

.inputText{
    padding: 0.5cm;
    font-size: 18px;
    border: thin solid rgb(141, 141, 141);
    box-shadow: 1.5px, 1.5px rgba(87, 87, 87, 0.486);
    width:7cm;
}

@media only screen and (min-width:300px) and (max-width:360px){
    .container{
        padding:0.7cm;
    }
    #result{
        width:6cm;
    }
    .inputText{
        width:5.5cm;
    }
    .b1, .b2, .b3{
        width:1.2cm;
        font-size: 14px;
        text-align: center;
        height: fit-content;
        padding:0.3cm;
    }
    .bimg{
        width:0.5cm;
        height:0.5cm;
    }
}

@media only screen and (min-width:250px) and (max-width:300px){
    .container{
        padding-top:0.5cm;
    }
    #result{
        width:5cm;
    }
    .inputText{
        width:4.5cm;
    }
    .b1, .b2, .b3{
        width:1cm;
        font-size: 11px;
        text-align: center;
        height: fit-content;
        padding:0.2cm;
    }
    .bimg{
        width:0.4cm;
        height:0.4cm;
    }
}

@media only screen and (max-width:250px){
    .container{
        padding-top:0.3cm;
        font-size: 16px;
    }
    #result{
        width:4cm;
    }
    .inputText{
        width:3.5cm;
    }
    .b1, .b2, .b3{
        width:0.8cm;
        border-radius: 5px;
        font-size: 11px;
        text-align: center;
        height: fit-content;
        padding:0.2cm;
    }
    .bimg{
        width:0.3cm;
        height:0.3cm;
    }
}